@import "fonts-mixin";
@import "fonts";
@import "nullstyle";

$minWidth: 320px;
$maxWidth: 1400px;
$maxWidthContainer: 1500px;
$md1: $maxWidthContainer + 12px;
$md2: 991.98px;
$md3: 767.98px;
$md4: 479.98px;

$fontFamily: 'Roboto';

::-webkit-scrollbar {
   width: 10px;
}
::-webkit-scrollbar-track {
   background-color: #38608be5;
   border-radius: 5px;
}
::-webkit-scrollbar-thumb {
   background-color: #0f223fec;
   border-radius: 5px;
   border: 2px solid #38608be5;
}

.wrapper {
   width: 100%;
   min-height: 100%;
   overflow: hidden;
   display: flex;
   flex-direction: column;
   font-family: $fontFamily;
   opacity: 0;
   transition: opacity .8s;
   //height: 100vh;
   &.loaded {
      opacity: 1;
      & .nav-page {
         opacity: 1;
         visibility: visible;
         transition: all 1s ease 1s;
      }
      & .sl-page__pagination {
         transform: translate(0 ,0);
         opacity: 1;
         visibility: visible;
         transition: transform .6s ease .8s;
      }
   }
   &.free {
      & .nav-page {
         opacity: 0;
         visibility: hidden;
         transition: all .3s;
      }
      & .sl-page__pagination {
         transform: translate(100px, 0);
         opacity: 0;
         visibility: hidden;
         transition: .3s;
      }
   }
   
}

.container {
   max-width: $maxWidthContainer;
   margin: 0 auto;
   padding: 0 15px;
}

@import "header";
@import "swiper";
@import "home";
@import "footer";
