.page {
   position: relative;
   flex: 1 1 auto;
   // .page__header
   &__header {
      background-color: #101d30be;
      border-radius: 10px;
      max-width: 500px;
      margin: 0 auto;
      padding: 10px;
      transition: .3s;
      &.hide {
         opacity: 0;
         visibility: hidden;
      }
      &_2 {
         max-width: 600px;
         margin-bottom: 50px;
      }
      &_3 {
         max-width: 300px;
      }
      @media (max-width: 650px) {
         //margin: 0 10px;
         width: 95%;
      }
   }
   // .page__title
   &__title {
      text-align: center;
      font-size: 40px;
      color: #CAF9FB;
      @media (max-width: 850px) {
         font-size: 30px;
      }
      @media (max-width: 500px) {
         font-size: 25px;
      }
   }
   // .page__video
   &__video {
      height: 100vh;
      video {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }
   // .page__content
   &__content {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
   }
   // .page__nav 
   &__nav {

   }
   // .page__slider
   &__slider {
      cursor: grab;
      transition: .5s;
      &.hide-slider {
         opacity: 0;
         visibility: hidden;
         height: 0;
      }
   }
   // .page__info
   &__info {
   }
}
.nav-page {
   position: fixed;
   top: 4.3%;
   right: 100px;
   //background-color: #101b2cb9;
   //background-color: rgba(0, 0, 0, 0.3);
   // padding: 15px;
   // border-radius: 10px;
   // display: flex;
   // flex-direction: column;
   // align-items: flex-end;
   z-index: 2;
   opacity: 0;
   visibility: hidden;
  
   // .nav-page__link
   &__link {
      color: rgba(255, 255, 255, 0.507);
      font-size: 20px;
      margin-bottom: 20.5px;
      transition: .3s;
      line-height: 1;
      margin-right: 30px;
      position: relative;
      &::before {
         content: "";
         position: absolute;
         width: 110%;
         height: 1px;
         top: -5px;
         animation: navLink .2s forwards;
         background-color: #fff;
      }
      &::after {
         content: "";
         position: absolute;
         width: 110%;
         height: 1px;
         bottom: -5px;
         animation: navLink .2s forwards;
         background-color: #fff;
      }
      &:last-child {
         margin-bottom: 0;
         margin-right: 0;
      }
      &.active {
         color: #fff;
         &::before, &::after {
            background-color: #fff;
            opacity: 1;
            animation: navLinkActive .2s forwards;
         }
      }
   }
}
.slider {
   .swiper-wrapper {
      padding: 50px 0;
      @media (max-width: 850px) {
         padding: 30px 0;
      }
      @media (max-width: 500px) {
         padding: 30px 0 30px 0;
      }
   }
   // .slider__slide
   &__slide {
      background-position: center;
      background-size: cover;
      width: 500px;
      transform-style: preserve-3d;
   }
}
.slide {
   padding: 20px;
   border: 4px solid #3f7ec2e5;
   border-radius: 20px;
   background-color: #101d30be;
   max-width: 100%;
   overflow: hidden;
   @media (max-width: 850px) {
      max-height: 300px;
      max-width: 400px;
   }
   @media (max-width: 500px) {
      max-height: 250px;
      max-width: 350px;
      width: 100%;
   }
   // .slide__body
   &__body {
      
   }
   // .slide__img
   &__img {
      border: 1px solid #3f7ec2e5;
      border-radius: 5px;
      overflow: hidden;
      height: 300px;
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
      @media (max-width: 850px) {
         height: 200px;
      }
      @media (max-width: 500px) {
         height: 150px;
      }
   }
   // .slide__title
   &__title {
      text-align: center;
      color:#FFF096;
      padding: 30px 0 20px 0;
      font-size: 30px;
      @media (max-width: 850px) {
         padding: 20px 0 10px 0;
         font-size: 25px;
      }
      @media (max-width: 500px) {
         font-size: 20px;
         padding: 20px 0 5px 0;
      }
   }
}
.info-page {
   &.show {
      position: absolute;
      top: 60%;
      left: 50%;
      width: 100%;
      transform: translate(-50%, 0);
      margin-bottom: 45px;
      animation: infoShow .3s forwards;
      .info-page__body {
         max-width: 100%;
      }
      .result-info__images {
         opacity: 0;
         height: 0;
         animation: imgShow .5s forwards .2s;
      }
      .result-info__title {
         opacity: 0;
         animation: resultTitle .5s linear normal running .8s forwards;
      }
      .result-info__text_derapture {
         animation: text .3s linear normal running .2s forwards;
      }
      .result-info__text_destination {
         animation: text .3s linear normal running .3s forwards;
      }
      .result-info__text_total, .result-info__distance_total {
         animation: text .3s linear normal running .5s forwards;
      }
      .result-info__btn {
         animation: btn 1s linear normal running .8s forwards;
      }
   }
   // .info-page__body
   &__body {
      border: 4px solid #3f7ec2e5;
      background-color: #101d30be;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 20px;
      max-width: 600px;
      margin: 0 auto;
      transition: .3s;
      margin-bottom: 45px;
   }
   // .info-page__content
   &__content {
      width: 100%;
   }
   // .info-page__choise
   &__choise {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin-bottom: 20px;
      &.hide-choise {
         display: none;
      }
   }
   // .info-page__label
   &__label {
      font-size: 30px;
      color:#CAF9FB;
      margin-bottom: 15px;
      span {
         font-size: 15px;
      }
      @media (max-width: 850px) {
         font-size: 25px;
         span {
            font-size: 12px;
         }
      }
      @media (max-width: 500px) {
         display: flex;
         flex-direction: column;
         justify-content: center;
         align-items: center;
         text-align: center;
         span {
            margin-top: 5px;
         }
      }
   }
   // .info-page__input
   &__input {
      width: 80%;
      justify-content: center;
      input {
         max-width: 100%;
      }
      input::-webkit-outer-spin-button,
      input::-webkit-inner-spin-button {
         -webkit-appearance: none;
      }
      input[type=number] {
         -moz-appearance:textfield; 
     }
     @media (min-width: 500px) {
        display: flex;
     }
     @media (max-width: 500px) {
        text-align: center;
        width: 100%;
     }
   }
   // .info-page__number
   &__number {
      border-radius: 10px;
      padding: 10px;
      font-size: 20px;
      color: #FFF096;
      background-color: #3f7ec2e5;
      text-align: center;
      margin-right: 10px;
      transition: .3s;
      flex: 0 1 70%;
      &.empty {
         animation: input 1.5s linear infinite alternate;
         @keyframes input {
            0% {
               box-shadow: 0 0 20px rgba(255, 0, 0, 0);
            }
            50% {
               box-shadow: 0 0 20px rgba(255, 0, 0, 0.9);
            }
            100% {
               box-shadow: 0 0 20px rgba(255, 0, 0, 0);
            }
         }
      }
      &:focus {
         background-color: #336396e5;
      }
      @media (max-width: 500px) {
         margin-bottom: 20px;
         margin-right: 0;
      }
   }
   // .info-page__btn
   &__btn {
      border-radius: 10px;
      padding: 10px;
      font-size: 20px;
      color:#CAF9FB;
      background-color: #3f7ec2e5;
      text-align: center;
      transition: all .3s;
      flex: 0 1 30%;
      @media (min-width: 850px) {
         &:hover {
            background-color: #2c5785e5;
            color:#e4f9fa;
         }
      }
   }
   // .info-page__result
   &__result {
      font-size: 20px;
      text-align: center;
      color: #FFF096;
   }
}
.result-info {
   display: none;
   opacity: 0;
   visibility: hidden;
   transition: .5s;
   &.show-result {
      display: block;
      opacity: 1;
      visibility: visible;
   }
   // .result-info__body
   &__body {
      display: flex;
      @media (max-width: 700px) {
         flex-direction: column;
      }
   }
   // .result-info__column
   &__column {
      &_img {
         flex: 0 1 50%;
         width: 100%;
      }
      &_text {
         padding: 0 0 20px 20px;
         width: 100%;
         flex: 0 1 50%;
         @media (max-width: 700px) {
            padding: 0 0 20px 0;
         }
      }
   }
   // .result-info__title
   &__title {
      font-size: 30px;
      @media (max-width: 700px) {
         font-size: 25px;
      }
   }
   // .result-info__img
   &__img {
      @media (min-width: 700px) {
         height: 450px;
      }
      img {
         width: 100%;
         margin-bottom: 35px;
         border: 1px solid #3f7ec2e5;
         border-radius: 5px;
         //height: 400px;
         object-fit: cover;
      }
      @media (max-width: 700px) {
         display: flex;
         flex-direction: column;
      }
   }
   // .result-info__images
   &__images {
      @media (max-width: 700px) {
         order: 2;
      }
   }
   // .result-info__title
   &__title {
      @media (max-width: 700px) {
         order: 1;
         margin-bottom: 20px;
      }
   }
   // .result-info__departure
   &__departure {
      border: 2px solid #3f7ec2e5;
      border-radius: 5px;
      margin-bottom: 30px;
      position: relative;
   }
   // .result-info__label
   &__label {
      display: inline-block;
      padding: 12px;
      width: 100%;
      border-bottom: 2px solid #3f7ec2e5;
      border-top: none;
      position: relative;
      background-color: #285688a9;
      flex: 1 1 auto;
      &_time {
         display: flex;
         justify-content: center;
         align-items: center;
      }
      span {
         font-size: 15px;
      }
      @media (max-width: 700px) {
         font-size: 15px;
      }
   }
   // .result-info__text
   &__text {
      font-size: 0;
      color: #C4FCFF;
      word-break: break-word;
   }
   // .result-info__destination
   &__destination {
      border: 2px solid #3f7ec2e5;
      border-radius: 5px;
      margin-bottom: 30px;
   }
   // .result-info__total
   &__total {
      margin-bottom: 30px;
      display: flex;
   }
   // .result-info__distance
   &__distance {
      border: 2px solid #3f7ec2e5;
      border-radius: 5px;
      flex: 0 1 50%;
      margin-right: 10px;
   }
   // .result-info__time
   &__time {
      border: 2px solid #3f7ec2e5;
      border-radius: 5px;
      flex: 0 1 50%;
      display: flex;
      flex-direction: column;
   }
   // .result-info__btn
   &__btn {
      border-radius: 10px;
      padding: 10px;
      font-size: 20px;
      color:#CAF9FB;
      background-color: #3f7ec2e5;
      text-align: center;
      transition: all .3s;
      opacity: 0;
      visibility: hidden;
      letter-spacing: .7px;
      @media (min-width: 850px) {
         &:hover {
            background-color: #2c5785e5;
            color:#e4f9fa;
         }
      }
   }
}
@keyframes btn {
   0% {
      opacity: 0;
      visibility: hidden;
   }
   100% {
      opacity: 1;
      visibility: visible;
   }
}
@keyframes text {
   0% {
      padding: 0;
      font-size: 0;
   }
   100% {
      padding: 30px 0;
      font-size: 20px;
   }
}
@keyframes infoShow {
   0% {
      top: 25%;
   }
   100% {
      top: 10%;
      width: 100%;
   }
}
@keyframes imgShow {
   0% {
      height: 0;
      opacity: 0;
   }
   100% {
      height: 100%;
      opacity: 1;
   }
}
@keyframes resultTitle {
   0% {
      opacity: 0;
   }
   100% {
      opacity: 1;
   }
}
@keyframes navLinkActive {
   0%{
      width: 0;
      left: 50%;
   }
   100% {
      width: 110%;
      left: -5%;
   }
}
@keyframes navLink {
   0%{
      width: 100%;
      left: -5%;
   }
   100% {
      width: 0;
      left: 50%;
   }
}
/*===========  Two page ==========*/
.sl-page {
   //position: relative;
   height: 100%;
   // @media (max-width: 900px) {
   //    .swiper-slide {
   //       height: auto;
   //    }
   // }
   // .sl-page__slide
   &__slide {
      //height: 100vh;
      height: 100%;
      overflow: hidden;
      padding: 20px 0 40px 0;
      &_1 {
         //overflow: auto;
      }
   }
   // .sl-page__border
   &__border {
   position: absolute;
   top: 13px;
   right: -15px;
   width: 0;
   height: 2px;
   background-color:#f8f6ea;
   transition: .2s;
   }
   // .sl-page__nav
   &__nav {
      transition: .3s;
      padding: 5px 10px;
      position: absolute;
      color: #d6f6f888;
      z-index: 1;
      &::before {
         content: "";
         position: absolute;
         top: 0;
         right: 0;
         width: 0;
         height: 1px;
         background-color:#f8f6ea;
         z-index: 1;
         transition: .2s;
         transition-delay: .1s;
      }
      &::after {
         content: "";
         position: absolute;
         bottom: 0;
         right: 0;
         width: 0;
         height: 1px;
         background-color: #f8f6ea;
         z-index: 1;
         transition: .2s;
         transition-delay: .1s;
      }
      &.active {
         font-size: 18px;
         color: #d7eef0;
         border-left: 1px solid #f8f6ea;
         border-right: 1px solid #f8f6ea;
         &::before, &::after {
            width: 100%;
         }
         .sl-page__border {
            width: 15px;
            top: 14px;
         }
      }
      &_1 {
         top: 15px;
         &.active {
            top: 13px;
         }
      }
      &_2 {
         top: 55px;
         &.active {
            top: 53px;
         }
      }
      &_3 {
         top: 95px;
         &.active {
            top: 93px;
         }
      }
   }
   // .sl-page__pagination
   &__pagination {
      position: absolute;
      z-index: 1;
      top: 82%;
      right: 60px;
      transform: translate(100px, 0);
      opacity: 0;
      visibility: hidden;
      .swiper-pagination-bullet {
         display: block;
         width: 20px;
         height: 20px;
         background-color:transparent;
         margin-bottom: 20px;
         border: 1px solid rgba(255, 255, 255, 0.3);
         border-radius: 2px;
         transform: rotate(45deg);
         transition: .2s;
         cursor: pointer;
         position: relative;
         &::after {
            content: "";
            position: absolute;
            width: 0;
            height: 0;
            top: 50%;
            left: 50%;
            color: #fff;
            font-size: 0;
            transition: .5s;
            text-align: center;
            z-index: 1;
            background-color: rgb(9, 173, 9);
            transition: .2s;
            border-radius: 2px;
         }
      }
      .swiper-pagination-bullet-active {
         border: 1px solid #f8f6ea;
         &::after {
            width: 12px;
            height: 12px;
            top: 3px;
            left: 3px;
         }
      }
      @media (max-width: 1024px) {
         display: none;
      }
   }
}
.sl-content {
   // .sl-content__body
   &__body {
      transition: .3s;
      &.hide {
         opacity: 0;
         visibility: hidden;
      }
   }
   // .sl-content__slide
   &__slide {
      display: flex;
      flex-wrap: wrap;
      overflow: hidden;
      position: relative;
      z-index: 5;
   }
   // .sl-content__item
   &__item {
   }
   // .sl-content__pagination
   &__pagination {
      color:#caf9fb;
      position: absolute;
      text-align: center;
      bottom: 10px;
      font-size: 30px;
      width: 100%;
   }
   // .sl-content__arrow
   &__arrow {
      display: flex;
      justify-content: center;
      fill: #3f7ec2e5;
      margin-top: 3%;
      position: relative;
      z-index: 1;
   }
   &__prev, &__next {
      cursor: pointer;
      margin: 0 40px;
      transition: .3s;
      &:hover {
         fill: #6ea0d6e5;
         transform: scale(1.1);
      }
      svg {
         width: 50px;
         height: 50px;
      }
   }
   // .sl-content__prev
   &__prev {
      
   }
   // .sl-content__next
   &__next {
      
   }
   // .sl-content__result
   &__result {
   }
}
.item-sl-content {
   padding: 0 15px;
   flex: 0 0 50%;
   margin-bottom: 30px;
   max-height: 300px;
   overflow: hidden;
   @media (max-width: 900px) {
      flex: 1 1 100%;
   }
   // .item-sl-content__body
   &__body {
      border: 4px solid #3f7ec2c5;
      border-radius: 10px;
      padding: 15px 15px 40px 15px;
