      text-align: center;
      font-size: 25px;
      color: #fff096;
      margin-bottom: 20px;
   }
   // .item-sl-content__row
   &__row {
      display: flex;
   }
   // .item-sl-content__img
   &__img {
      border: 1px solid #3f7ec2e5;
      border-radius: 5px;
      flex: 0 0 40%;
      margin-right: 10px;
      height: 170px;
      overflow: hidden;
      img {
         height: 100%;
         width: 100%;
         object-fit: cover;
         &:nth-child(2) {
            display: none;
         }
      }
   }
   // .item-sl-content__text
   &__text {
      flex: 0 0 60%;
      color: #caf9fb;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      p {
         padding: 0 10px;
         font-size: 16px;
         line-height: 1.2;
         margin-bottom: 10px;
         height: 114px;
         overflow: hidden;
      }
   }
   // .item-sl-content__btn
   &__btn {
      color: #fdf3b7;
      cursor: pointer;
      transition: .3s;
      margin-bottom: 10px;
      padding: 0 10px;
      text-align: end;
      .show-slide-btn {
         display: block;
      }
      &:hover {
         color:   #c7bf90;
      }
   }
   // .item-sl-content__dist
   &__dist {
      font-size: 17px;
      color: #fdf3b7;
   }
   // .item-sl-content__close
   &__close {
      position: absolute;
      top: 10px;
      right: 10px;
      color: #fff;
      font-size: 25px;
      cursor: pointer;
      display: none;
      z-index: 10;
   }
}
.result-content {
   position: absolute;
   top: 10%;
   left: 50%;
   width: 80%;
   transform: translate(-50%, 0);
   z-index: 5;
   border: 4px solid #3f7ec2e5;
   border-radius: 10px;
   background-color: #101d30ec;
