      color: #fff096;
   }
   // .result-content__body
   &__body {
      display: flex;
   }
   // .result-content__img
   &__img {
      border: 1px solid #3f7ec2e5;
      border-radius: 5px;
      flex: 0 0 50%;
      overflow: hidden;
      height: 520px;
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }
   // .result-content__text
   &__text {
      display: flex;
      flex-direction: column;
      padding: 15px;
      color: #caf9fb;
      font-size: 18px;
      line-height: 1.3;
      max-height: 600px;
      overflow: auto;
   }
   // .result-content__description
   &__description {
      flex: 1 1 auto;
